<!-- 下注 -->
<template>
  <div class="page qk-lottery-betting" v-nav="{hideNavbar: true}">
    <div class="page-content">
      <div class="top-part">
          <div class="prize-and-time">
            <div class="trapezoid">
              <div class="left-panel">
                <span>本期奖池</span>
                <span v-text="jackpot"></span><span class="yuan">元</span>
              </div>
              <div class="separator">
                <div class="hairline-stand-alone vertical"></div>
              </div>
              <div class="left-panel">
                <span>剩余时间</span>
                <span class="timer">
                  <count-down class="time-deadline" :deadline="end_time" :endcallback="countdownComplete"></count-down>
                </span>
              </div>
            </div>
          </div>
          <div class="my-chips" @click="goTo('/lottery/getChips')">
            <img src="//qianka.b0.upaiyun.com/images/ad5932487216be512a689d565d239a17.png" alt="筹码" class="multiple-chips chips-image">
            <span class="amount">您有 <span v-text="available_chips"></span> 个筹码</span>
            <div class="get-chips">
              <span>获取筹码</span>
              <img src="//qianka.b0.upaiyun.com/images/0a6507fefa944de03fe165f421cfbc77.png" alt="" class="arrow-left">
            </div>
          </div>
      </div>
      <div class="bottom-part">
        <!-- 赌桌 -->
        <div class="select-chips-box">
          <div class="chip-row" v-for="row in [0, 1, 2, 3]" :key="row">
            <span class="chip" :class="{'selected': current_bets[getNum(row, col)]}" v-for="col in [0, 1, 2, 3]" :key="col" @click="chipSelected(row, col)">
              <span class="chip-value" v-text="getNum(row, col)"></span>
              <div class="selected-chips" v-if="chipsInCell(row, col)">
                <img v-if="chipsInCell(row, col) > 1" src="//qianka.b0.upaiyun.com/images/ad5932487216be512a689d565d239a17.png" alt="筹码" class="multiple-chips chips-image">
                <img v-if="chipsInCell(row, col) === 1" src="//qianka.b0.upaiyun.com/images/0a40ada080e85ee8ab2fa49957fe3795.png" alt="筹码" class="single-chip chips-image">
                <span class="selected-num" v-text="'x' + chipsInCell(row, col)"></span>
              </div>
              <span class="chip-selection-num" v-if="totalChipsInCell(row, col)" v-text="totalChipsInCell(row, col) + '注'"></span>
            </span>
          </div>
        </div>
      </div>
    </div>

    <button class="button btn-back" @click="back"></button>

    <div class="footer">
      <button class="btn reset" @click="resetCurrentBets()">重置</button>
      <button class="btn bet" @click="bet()">下注</button>
    </div>
  </div>
</template>
