            display: block;
          }

          &:nth-child(2) {
            @include font-dinpro-medium();
          }
        }

        .yuan {
          font-size: 12px;
          line-height: 17px;
          vertical-align: baseline;
          display: inline-block;
          margin-left: 1px;
          position: relative;
