          @include font-regular();
        }
      }
    }

    .top-part {
      height: 114px;
      background: url(//qianka.b0.upaiyun.com/images/9b594bd8ef0245e36143fd5b8ab941f3.png) center top no-repeat;
      background-size: 100% 92px;

      .prize-and-time {
        height: 50px;
        width: 255px;
        margin: auto;
        background: url(//qianka.b0.upaiyun.com/images/f4f61bd2edbccbf219a2f2cae99cc117.png) center top no-repeat;
        background-size: 100% 50px;
