      .my-chips {
        height: 40px;
        line-height: 40px;
        width: 303px;
        margin: 20px auto;
        background: #873C96;
        box-shadow: inset 0 -4px 0 0 rgba(0, 0, 0, 0.1);
        border-radius: 4px;
        color: white;
        font-size: 14px;
        position: relative;

        .amount,
        .get-chips,
        .chips-image {
          display: inline-block;
          vertical-align: middle;
        }

