            }

            .chip-selection-num {
              font-size: 12px;
              display: block;
              position: absolute;
              left: 12px;
              bottom: 10px;
              opacity: 0.5;
            }

            .selected-chips {
              .selected-num {
                position: absolute;
                right: 8px;
                top: 37px;
                font-size: 18px;
                color: #FFAA1E;
