  .qk-lottery-betting .footer {
    background: #342755;
    height: 56px;
    position: absolute;
    width: 100%;
    bottom: 0;
    padding: 8px 16px;
    overflow: hidden;

    .btn {
      background-color: #873C96;
      width: 164px;
      padding: 0;
      display: block;
      float: left;
      text-align: center;
      font-size: 14px;
      line-height: 40px;
      height: 40px;
      border-radius: 2px;
      color: white;
      border: none;

      &.bet {
        background-color: #FFAA1E;
        margin-left: 15px;
      }
