</style>
<script>
  /* eslint eqeqeq: 'off', camelcase: 'off' */
  import { popup_lotterty_confirm_bet } from './popups'
  import CountDown from '../widget/CountDown.vue'

  const BET_LIMIT = 5

  const initBets = () => {
    let bets = {}
    _.map(_.range(16), (i) => {
      bets[i + 3] = 0
    })
    return bets
  }

  const getNum = (row, col) => {
    return row * 4 + col + 3
  }

  export default {
    components: {
      CountDown
    },

    data () {
      return {
        jackpot: '', // 当期奖池金额
        lottery_id: undefined, // 开奖期数
        available_chips: 0, // 可用筹码数
        bets: initBets(), // 赌桌注数
        my_bets: {}, // 本人已投注
        current_bets: {}, // 本人本次投注
        // start_time: 0,
        end_time: 0
      }
    },

    mounted () {
      this.fetchData()
    },

    methods: {
      resetBeforeFetch () {
        _.extend(this.$data, {
          available_chips: 0, // 可用筹码数
          bets: initBets(), // 赌桌注数
          my_bets: {}, // 本人已投注
          current_bets: {} // 本人本次投注
        })
      },

      fetchData () {
        $api.lotteryGamingTable()
          .then(res => res.data && res.data.payload)
          .then(payload => {
            let bets = {}

            for (let i in payload.bets_info) {
              bets[i] = payload.bets_info[i].bets
            }

            _.extend(this.$data, {
              lottery_id: payload.lottery_id,
              jackpot: payload.jackpot,
              end_time: moment(payload.end_time, 'YYYY-MM-DD hh:mm:ss').valueOf(),
              // start_time: moment(payload.start_time, 'YYYY-MM-DD hh:mm:ss').valueOf(),
              available_chips: payload.available_chips,
              bets: bets,
              my_bets: payload.my_bets
            })
          })
      },

      getNum: getNum,

      chipsInCell (row, col) {
        let num = getNum(row, col)
        return (this.my_bets[num] || 0) + (this.current_bets[num] || 0)
      },

      totalChipsInCell (row, col) {
        return this.bets[getNum(row, col)] || 0
      },

      chipSelected (row, col) {
        if (_.sum(_.values(this.my_bets)) + _.sum(_.values(this.current_bets)) >= BET_LIMIT) {
          $toast.show(`每期最多使用${BET_LIMIT}个筹码`)
          return
        }

        if (this.available_chips < 1) {
          $toast.show('剩余筹码不足')
          return
        }

        this._chipMutation(getNum(row, col), 1)
      },

      _chipMutation (num, qty) {
        this.current_bets[num]
          ? this.current_bets[num] += qty
          : this.current_bets[num] = qty

        this.available_chips -= qty
      },

      resetCurrentBets () {
        this.available_chips += _.sum(_.values(this.current_bets))
        this.current_bets = {}
      },

      bet () {
        if (this.jackpot && (parseFloat(this.jackpot) <= 0)) {
          $toast.show('本期奖池为0，暂无法下注')
          return
        }

        if (_.sum(_.values(this.my_bets)) + _.sum(_.values(this.current_bets)) > BET_LIMIT) {
          $toast.show(`每期最多使用${BET_LIMIT}个筹码`)
          return
        }

        if (_.values(this.current_bets).length < 1) {
          $toast.show('请至少选择一个数字')
          return
        }

        let remainingBets = BET_LIMIT - _.sum(_.values(this.my_bets)) - _.sum(_.values(this.current_bets))
        let popup = $popup.fromTemplate(
          popup_lotterty_confirm_bet.template(remainingBets),
          popup_lotterty_confirm_bet.options
        )

        popup.show().then((index) => {
          if (index == 0) {
            return
          } else if (index == 1) {
            $api.lotteryBet({
              lottery_id: this.lottery_id,
              bets: this.current_bets
            })
            .then((res) => {
              $toast.show('下注成功')
              this.resetBeforeFetch()
              this.fetchData()
            },
            _ => {
              this.resetCurrentBets()
            })
          }
        })
      },

      back () {
        $router.back('/lottery')
      },

      countdownComplete () {
        window.location.reload()
      },

      goTo (path) {
        $router.forward({
          path: path,
          query: { backroute: '/lottery/betting' }
        })
      }
    }
  }
</script>



