/* eslint eqeqeq: "off", camelcase: "off" */
require('./popups.scss')

// 确认下注弹层
const popup_lotterty_confirm_bet = {
  options: {
    effect: 'scale',
    cssClass: 'popup-lotterty-confirm-bet',
    buttons: [
      {text: '取消'},
      {text: '确定'}
    ]
  },
  template: (remainingBets) => `
    <div class="desc no-title">确认下注? 本期还可使用 <span class="assertive"> ${remainingBets} </span> 个筹码</div>
  `
}

module.exports = {
  popup_lotterty_confirm_bet
}



